<template>
    <v-container>
        <v-app-bar
                fixed
                app
                color="#FFFFFF"
                elevation="3"
        >
            <v-row>
                <!--<v-col></v-col>-->
                <v-col class="text-center">
                    <v-toolbar-title>{{chatsText}}</v-toolbar-title>
                </v-col>
                <!-- <v-col class="text-right" style="padding-top: 0; padding-bottom: 0"><v-btn class="app-bar-button" small style="display: inline;" text color="primary"
                                                                                            @click="openProfileEdit">{{editText}}
                 </v-btn></v-col>-->
            </v-row>


        </v-app-bar>
        <div style="margin-top: 54px"/>
        <v-row>
            <v-col class="pa-0">
                <v-list three-line class="ma-0 pa-0">
                    <template v-for="(item, index) in items">
                        <v-subheader
                                v-if="item.header"
                                :key="item.header"
                                v-text="item.header"
                        ></v-subheader>

                        <v-divider
                                v-else-if="item.divider"
                                :key="index"
                                :inset="item.inset"
                        ></v-divider>

                        <v-list-item
                                v-else
                                :key="item.title"
                                @click="openChatWithUser(item.ids)"
                        >
                            <v-list-item-avatar>
                                <v-img :src="item.avatar"></v-img>
                            </v-list-item-avatar>

                            <v-list-item-content>
                                <v-list-item-title>
                                    {{item.title}}
                                    <span v-show="item.notReadMessages != 0" id="notReadMessagesNotification">{{item.notReadMessages}}</span>
                                </v-list-item-title>
                                <v-list-item-subtitle v-html="item.subtitle"></v-list-item-subtitle>
                            </v-list-item-content>
                        </v-list-item>
                    </template>
                </v-list>
            </v-col>
        </v-row>

        <v-card v-if="numberOfChats == 0" class="pa-2 ma-1" style="padding-bottom: 24px!important; height: 76.5vh; padding-top: 24px!important">
            <v-row class="text-center justify-center">
                <p class="pa-4" style="opacity: 0.6; padding-bottom: 0px!important">{{noChatsText}}</p>
                <img :src="sadFaceImg" style="opacity: 0.2; width: 22.5%; max-width: 150px; min-width: 65px">
            </v-row>
        </v-card>
    </v-container>
</template>

<script>
    import {mapActions, mapState, mapGetters} from 'vuex';
    import Constants from "../../store/Constants";

    export default {
        name: "ChatHome",
        data() {
            return {
                chatsText: Constants.TEXT_CHATS,
                numberOfChats: null,
                noChatsText: Constants.TEXT_NO_CHATS_AVAILABLE,
                sadFaceImg: Constants.IMG_EMOJI_SAD_FACE,
            }
        },
        methods: {
            /*...mapActions([
                'prepareChatWithUser'
            ]),*/
            openChatWithUser(itemIDs) {
                this.$router.push('chats/chat/' + itemIDs.chat);
            }
        },
        computed: {
            ...mapGetters('chats', ['getChatsToShow', 'getNotReadMessagesCountByChatID', 'getUserByChatID']),

            items: function () {
                let self = this;
                let itemsChatList = [];
                let nChats = 0;

                self.getChatsToShow.forEach(chat => {

                    let user = self.getUserByChatID(chat.id);

                    itemsChatList.push({
                        ids: {
                            chat: chat.id,
                            user: user.id
                        },
                        avatar: user.profileImg,
                        title: user.username,
                        subtitle: chat.lastMessage.text,
                        notReadMessages: self.getNotReadMessagesCountByChatID(chat.id)
                    });

                    nChats++;
                });

                this.numberOfChats = nChats;
                return itemsChatList;
            },
        },
    }
</script>

<style scoped>
    #notReadMessagesNotification {
        font-size: 12px;
        text-align: center;
        display: inline-block;
        background-color: lightskyblue;
        margin-left: 5px;
        min-width: 22px!important;
        padding: 4px;
        border-radius: 50%;
    }
</style>